.odp_search_form_wrapper{
  input, select{
    border:1px solid #ccc;
    margin:0;
    width:100%;
    height: 50px;
    line-height: 50px;
    padding: 0 15px;

    &:focus{
      outline: none;
    }
  }
  &.in_line{
    padding:10px;
    form{
      display: flex;
      @media only screen and (max-width: 991px) {
        display: block;
      }
      .field{
        display:flex;
        flex-direction: column;
        width:100%;
        margin:15px 0;

        &.datepicker_field{
          margin:0 0 5px 0;
        }
        &.submit_field{
          margin:5px 0 0 0;
        }

        @media only screen and (min-width: 992px) {
          margin:0 5px;

          &.datepicker_field{
            margin:0 5px 0 0;
          }
          &.submit_field{
            margin:0 0 0 5px;
          }
        }
        &.hidden{
          display:none;
        }

        &.code_field{

        }
        &.guests_field{

        }
        &.ordering_field{

        }
        &.submit_field{
          flex-direction: column-reverse;
        }
        &.datepicker_field{

        }
      }
    }
  }
  &.in_column{
    form{
      display: block;
      .field{
        margin: 15px 0;
        display: flex;
        flex-direction: column;
        &.datepicker_field{
          margin:0 0 5px 0;
        }
        &.submit_field{
          margin:5px 0 0 0;
        }
        &.hidden{
          display:none;
        }

        label{
          margin:0 10px;
        }
      }

    }
  }
}

.odp_search_results-table{
  width:100%;
  font-size: 14px;
  border: 1px solid #f2f6fc;
  h3{
    margin:10px;
  }
  h4{
    font-size:17px;
    margin: 0;
  }
  .tr{
    width:100%;
    border-bottom: 1px solid #f2f6fc;
    text-align: center;

    @media only screen and (min-width: 992px) {
      display: flex;
      text-align: left;
    }
    .td{
      padding:15px 10px;

      @media only screen and (max-width: 991px) {
        &:empty {
          display: none;
        }
      }

      &.title{
        border-right: 1px solid #f2f6fc;
        @media only screen and (min-width: 992px) {
          width:35%;
        }
        img{
          margin-bottom:15px;
        }
      }
      &.features{
        border-right: 1px solid #f2f6fc;

        @media only screen and (min-width: 992px) {
          width:35%;
        }
      }
      &.cost{
        @media only screen and (min-width: 992px) {
          width:30%;
        }

        .cost {
          margin-bottom: 10px;
        }
        .cost-wrapper {
          display: inline-block;
          width: auto;
          text-align: center;
        }
      }
    }

    &.head{
      .td{
        background: #f2f6fc;
      }
    }
  }
}

.odp_hotels_map_wrapper{
  margin:30px 0;
  .odp_hotels_map{
    margin-top:20px;
    height:400px;
  }
}

.odp_search_results-empty{
  border:1px solid red;
  border-radius:5px;
  padding:3px 10px;
}

.rates{
  width:100%;

  tr{
    th, td {
      text-align: left;

      @media only screen and (max-width: 991px) {
        &:nth-child(3),
        &:nth-child(4),
        &:nth-child(5) {
          display: none;
        }

        .rate_plan_name {
          font-weight: bold;
        }
      }

      @media only screen and (min-width: 992px) {
        .mobile {
          display: none;
        }
      }

      &:last-child{
        width:50px;
      }
    }
  }

  &.loading{
    background: url('../images/loading.gif') no-repeat center center;
    background-size:30px 30px;
    opacity: 0.7;
  }

  form{
    input{
      padding:0 0 0 10px;
      width:50px;
      text-align: center;

    }
  }
}

.hotel_description{
