.room_resource_check{
  margin-right:20px;
}
.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 27px;
  margin:0;

  input{
    display:none;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 27px;

    &:before {
      position: absolute;
      content: "";
      height: 20px;
      width: 20px;
      left: 4px;
      bottom: 4px;
      background-color: white;
      transition: .4s;
      border-radius: 50%;
    }
    &:after {
      content:'';
      color: white;
      display: block;
      position: absolute;
      transform: translate(-50%, -50%);
      top: 50%;
      left: 50%;
    }
  }

  input:checked + .slider {
    background-color: rgb(57, 65, 77);
  }

  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }

  input:checked + .slider:before {
    transform: translateX(22px);
  }


  input:checked + .slider:after
  {
    content:'';
  }
}

